<template>
    <b-container fluid>
        <h3 class="mb-4"><i class="fas fa-calendar-times"></i> Pedidos Detenidos</h3>
        <hr>
        <div v-if="items.length > 0">
            <!-- User Interface controls -->
            <b-row class="my-2">
                <b-col md="4">
                    <b-form-group label="Filtrar"
                                label-cols-sm="1"
                                label-cols-md="2"
                                label-size="sm"
                                label-for="filterInput">
                        <b-input-group size="sm">
                            <b-form-input v-model="filter"
                                            type="search"
                                            id="filterInput"
                                            placeholder="Escribe el filtro..."
                                            autocomplete="off">
                            </b-form-input>
                            <b-input-group-append>
                                <b-button :disabled="!filter" @click="filter = ''">
                                    <i class="fas fa-times"></i>
                                </b-button>
                            </b-input-group-append>
                        </b-input-group>
                    </b-form-group>
                </b-col>
                <b-col md="3">
                    <b-form-group label="Por página"
                                    label-cols-sm="2"
                                    label-cols-md="2"
                                    label-cols-lg="4"
                                    label-size="sm"
                                    label-for="perPageSelect">
                        <b-form-select v-model="perPage"
                                        id="perPageSelect"
                                        size="sm"
                                        :options="pageOptions">
                        </b-form-select>
                    </b-form-group>
                </b-col>
            </b-row>
            <!-- Main table element -->
            <b-table small
                    stacked="md"
                    head-variant="dark"
                    :hover="hover"
                    :striped="striped"
                    :bordered="bordered"
                    :items="items"
                    :fields="fields"
                    :current-page="currentPage"
                    :per-page="perPage"
                    :filter="filter"
                    :filterIncludedFields="filterOn"
                    :sort-by.sync="sortBy"
                    :sort-desc.sync="sortDesc"
                    :sort-direction="sortDirection"
                    @filtered="onFiltered">
                <template v-slot:cell(estatus)="row">
                    <b-badge variant="danger" v-if="row.item.estatus == 'DA'" class="p-2">Detenido, requiere autorización</b-badge>
                    <b-badge variant="danger" v-if="row.item.estatus == 'DC'" class="p-2">Detenido Límite Crédito</b-badge>
                </template>
                <template v-slot:cell(observacion)="row">
                    <b-badge variant="primary" class="p-2" @click="VerObservacion(row)" v-if="row.item.observacion != ''">Ver</b-badge>
                </template>
                <template v-slot:cell(actions)="row">
                    <b-button @click="AutorizarPedido(true, row.item)"
                                title="Aprobar"
                                size="sm"
                                class="mr-1"
                                v-b-tooltip.hover
                                variant="success">
                        <i class="fa fa-check"></i>
                    </b-button>
                    <b-button @click="AutorizarPedido(false, row.item)"
                                title="Rechazar"
                                size="sm"
                                class="mr-1"
                                v-b-tooltip.hover
                                variant="danger">
                        <i class="fa fa-times"></i>
                    </b-button>
                </template>
            </b-table>
            
            <b-pagination v-model="currentPage"
                            class="mt-1"
                            :total-rows="totalRows"
                            :per-page="perPage"
                            align="center"
                            aria-controls="my-table">
            </b-pagination>
        </div>
        <div v-else>
            <b-alert show variant="warning">No se encontraron registros de <strong>clientes detenidos</strong></b-alert>
        </div>
    </b-container>
</template>

<script>

import servicio from '@/services/servicio-pedidos'
import Swal from 'sweetalert2'

export default {
    data(){
        return {
            items: [],
            credenciales:{
                usuario: '',
                password: '',
            },
            totalRows: 1,
            currentPage: 1,
            perPage: 5,
            pageOptions: [5, 10, 15],
            sortBy: "",
            sortDesc: false,
            bordered: true,
            hover: true,
            striped: true,
            sortDirection: "asc",
            filter: null,
            filterOn: [],
            fields: [
                {
                    key: "folioPedido",
                    label: "Folio Pedido",
                    sortable: true,
                    sortDirection: "desc",
                },
                {
                    key: "cliente",
                    label: "Cliente",
                    sortable: true,
                    class: "text-center",
                },
                {
                    key: "formatoCantidad",
                    label: "Cantidad",
                    sortable: true,
                    class: "text-center",
                },
                {
                    key: "formatoPorcentajeDescuento",
                    label: "Descuento",
                    sortable: true,
                    class: "text-center",
                },
                {
                    key: "fechaSalida",
                    label: "Fecha Salida",
                    sortable: true,
                    class: "text-center",
                },
                { key: "estatus", label: "Estatus", sortable: true, class: "text-center" },
                { key: "observacion", label: "Observación", class: "text-center" },
                { key: "actions", label: "", class: "text-center" },




            ],

            pedidoAutorizado: false,
            observacion: '',
            pedidoSeleccionado: [],
        }
    },
    components: {
    },
    computed: {
        sortOptions() {
        // Create an options list from our fields
        return this.fields
            .filter((f) => f.sortable)
            .map((f) => {
            return { text: f.label, value: f.key };
            });
        },
    },
  async mounted() {
        await this.ObtenerPedidosDetenidos();
        await this.CargarRenglones();
  },
  methods: {
    
    onFiltered(filteredItems) {
      // Trigger pagination to update the number of buttons/pages due to filtering
      this.totalRows = filteredItems.length;
      this.currentPage = 1;
    },

    async ObtenerPedidosDetenidos () {
        this.items = [];
        this.$loading(true);
        await servicio.obtenerPedidosDetenidos(0)
                        .then(response => {
                            this.$loading(false);
                            if(response.value){
                                this.items = response.data;
                            }
                        })
                        .catch(error => {
                            this.$loading(false);
                            console.log(error)
                        })
    },

    CargarRenglones() {
        setTimeout(() => {
        this.totalRows = this.items.length;
        }, 1000);
    },

    async AutorizarPedido(autorizado, item) {
        this.pedidoAutorizado = autorizado;
        this.pedidoSeleccionado = item;
        let accion = autorizado ? "ACEPTAR" : "CANCELAR";

        let r = await Swal.fire({
            text: "¿Desea "+ accion +" el pedido del cliente: " +item.cliente+ "?",
            icon: "question",
            showCancelButton: true,
            confirmButtonColor: "#3085d6",
            cancelButtonColor: "#d33",
            confirmButtonText: "Aceptar",
            cancelButtonText: "Cancelar",
        });

        if (r.value) {
            await this.CapturarObservacion();
        }

    },

    async CapturarObservacion() {

        let observacion;

        const { value: formValues } = await Swal.fire({
            title: 'Ingrese una observación:',
            html:
                '<textarea id="swal-input2" class="swal2-input" placeholder="Capture una observación" style="margin-top: 20px; margin-bottom: 20px; height: 120px;">',
            focusConfirm: false,
            showCancelButton: true,
            confirmButtonText: 'Aceptar',
            cancelButtonText: 'Cancelar',
            confirmButtonColor: '#3085d6',
            cancelButtonColor: '#d33',

            preConfirm: () => {

                observacion = Swal.getPopup().querySelector('#swal-input2').value

                let inputObservacion = Swal.getPopup().querySelector('#swal-input2');

                inputObservacion.style.borderColor = "";

                if (observacion === '') {
                    inputObservacion.style.borderColor = "red";
                    Swal.showValidationMessage(`Favor de ingresar una observación.`)
                }
            },

        })

        if (formValues) {
            this.observacion = observacion;
            await this.AutorizarPedidoDetenido();
        }
        // else {
        //     this.procederGuardado = false;
        //     this.errorUsuario = false;
        // }

    },

    async AutorizarPedidoDetenido() {

            let accion = this.pedidoAutorizado ? "ACEPTO" : "CANCELO";
            this.$loading(true);
            await servicio
                    .autorizarPedidoDetenido(this.pedidoSeleccionado.folioPedido, this.pedidoAutorizado, this.observacion)
                    .then((r) => {
                        if(r.value) {
                            Swal.fire(
                                '',
                                'Se ' + accion + ' el pedido detenido correctamente',
                                'success'
							);
                            this.ObtenerPedidosDetenidos();
                        }// else {}
                    })
                    .catch((error) => {
                        console.log(error);
                        this.$loading(false);
                    });
            this.$loading(false);
        },

        VerObservacion(item) {
            this.$swal.fire({
                text: item.item.observacion,
                icon: "info"
            })
        }
  
  },

}
</script>