servicio.guardarPedido = (pedido) => {
    return base.post("/pedidos/guardar", pedido, {
            headers: {
                'Content-Type': 'application/json'
            }
        })
        .then(r => {
            return r.data
        })
        .catch(e => {
            console.log(e);
        })
}

